import "./sman.css";
import React from "react";

import SMANCard from "./../sman-card/sman-card";
import domtoimage from 'dom-to-image';

function SMAN() {
    const _date = Date.now()
    const [{name, branch, position, date}, setSMANCardOpts] = React.useState({
        name: "",
        position: "",
        branch: "",
        date: _date
    })
    const [imgSrc, setImgSrc] = React.useState(null)
    const [loading, setLoading] = React.useState(false)

    function generateIDCard() {
        setLoading(true)
        window.smancard.style.overflow = "hidden"
        domtoimage.toJpeg(window.smancard, { quality: 1 })
        .then(function (dataUrl) {
            var link = document.createElement('a');
            link.download = `sman-id-card-${name.split(" ").join("-")}-${Date.now()}.jpeg`;
            link.href = dataUrl;
            link.click();
            setLoading(false)
        }).catch((err) => {
            setLoading(false)
            alert(err)
        })
        /*
        domtoimage.toPng(window.smancard)
        .then(function (dataUrl) {
            var img = new Image();
            img.src = dataUrl;
            document.body.appendChild(img);
            window.smancard.style.overflow = ""
            setLoading(false)
        })
        .catch(function (error) {
            console.error('oops, something went wrong!', error);
        });*/
    }
    function handleImage(e) {
        var file = e.target.files[0]
        var fileReader = new FileReader()
        fileReader.readAsDataURL(file);
        fileReader.onloadend = function(e) {
           setImgSrc([fileReader.result])
        }
    }
    return (
        <div className="mainSMAN">
            <div id="op">
                <div className="SMANInputArea">
                    <input onChange={(e) => setSMANCardOpts({ date, branch, position, name: e.target.value })} id="name" maxLength="29" type="text" placeholder="Name here... (max: 29)" />
                </div>
                <div className="SMANInputArea">
                    <input onChange={(e) => setSMANCardOpts({ date, name, branch, position: e.target.value })} id="posiiton" maxLength="29" type="text" placeholder="Position here... (max: 29)" />
                </div>
                <div className="SMANInputArea">
                    <input onChange={(e) => setSMANCardOpts({ date, branch: e.target.value, name, position })} id="branch" maxLength="29" type="text" placeholder="Branch here... (max: 29)" />
                </div>
                <div className="SMANInputArea">
                    <input onChange={handleImage} id="branch" maxLength="20" type="file" placeholder="Picture here..." accept=".jpeg,.png,.jpg" />
                </div>
            </div>
            <div className="idCardPreview" id="idCard">
                <SMANCard {...{ name, branch, position, date, imgSrc }} />
            </div>
            <div className="SMANInputArea">
                <button className="button-block" disabled={loading} onClick={() => generateIDCard()}>Generate ID Card</button>
            </div>
        </div>
    )
}

export default SMAN